import {metadata} from "aurelia-metadata";

// some keys
const metadataKey = Symbol();

/**
* Property decorator, adds metadata about this property
*/
export function entity({ collection, key }) {

  return function( target ) {
    target[metadataKey] = { collection, key  };
  };

}

/**
* Returns entity metadata for an object
*/
export function getEntityMetadata( target ) {
  return target[metadataKey] || {};
}
